import PageButton from '../../components/button.vue';

export default {
	components: {

		PageButton
	},
	data() {
		return {
			temp:{
				time:''
			},
			time:0,
		}
	},
	onLoad(options) {
		this.time = options.time;
	},
	methods: {
		submit: function() {
			let that = this;
			that.$util.request({
				url: "/didi-patient/patientrefundinfo/wxRefund",
				param: that.temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.setStorageSync('patientInfo', res.data);
					uni.showToast({
						title: '退款成功',
						icon: 'success',
						duration: 2000
					})
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 500)
				},
				error: function() {
					
				}
			})
		}
	}
}
